  transform: transform3d(0, -2px, 0);
}

.hover-after,
.site-footer li.menu-item a,
li.menu-item a {
  position: relative;
  display: inline-block;
  overflow: hidden;
  padding-bottom: 1px;
  transition: opacity 250ms 0ms cubic-bezier(0.77, 0, 0.175, 1), transform 250ms 0ms cubic-bezier(0.77, 0, 0.175, 1);
}
.hover-after::after,
.site-footer li.menu-item a::after,
li.menu-item a::after {
  transition: opacity 250ms 0ms cubic-bezier(0.77, 0, 0.175, 1), transform 250ms 0ms cubic-bezier(0.77, 0, 0.175, 1);
  top: auto;
  bottom: 0;
  height: 1px;
  opacity: 0.35;
  transform: translateX(0%);
  background: currentColor;
}
.hover-after::before,
.site-footer li.menu-item a::before,
li.menu-item a::before {
  transition: opacity 250ms 0ms cubic-bezier(0.77, 0, 0.175, 1), transform 250ms 0ms cubic-bezier(0.77, 0, 0.175, 1);
  top: auto;
  bottom: 0;
  height: 1px;
  opacity: 1;
  transform: translateX(110%);
  background: currentColor;
}
.hover-after:hover,
li.menu-item a:hover {
  transform: translateY(-2px);
}
.hover-after:hover::after,
li.menu-item a:hover::after {
  transform: translateX(-110%);
}
.hover-after:hover::before,
li.menu-item a:hover::before {
  transform: translateX(0%);
}

