.vidbox {
  position: relative;
  width: 100%;
  overflow: hidden;
}
.vidbox:before {
  content: "";
  display: block;
  width: 100%;
  padding-bottom: 58%;
}
.vidbox.type--url, .vidbox.type--URL {
  width: 100%;
  background: black;
}
.vidbox .fallback-image,
.vidbox [data-fallback-image] {
  position: absolute;
  top: 0;
  lefT: 0;
  width: 100%;
  height: 100%;
}
.vidbox .fallback-image img,
.vidbox [data-fallback-image] img {
  object-fit: cover;
}
.vidbox.video-banner {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  pointer-events: none;
  background-color: black;
}
.vidbox__video {
  transition: opacity 1s linear;
  opacity: 0;
  z-index: 10;
  pointer-events: none;
}
.vidbox video.vidbox__video {
  opacity: 1;
}
.vidbox .fallback {
  transition: opacity 1s linear;
}
.vidbox .fallback.fallback-gif {
  opacity: 0;
}
.vidbox.use-vid .vidbox__video {
  opacity: 1;
}
.vidbox.use-gif .fallback.fallback-gif {
  opacity: 1;
}
.vidbox iframe,
.vidbox video {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 130%;
  width: 150%;
  transform: translate3d(-50%, -50%, 0);
  opacity: 0;
  transition: opacity 400ms ease;
  min-width: 150vh;
  min-height: 80vw;
}
